      <p class="text-intro">
        다양한 사람들과 어울리는걸 좋아해 서비스업을 선택하고 오래 일을
        해왔습니다. <br />
        우연히 사람들을 편리하게 해주는 앱을 알게되고 흥미를 가져 스스로
        공부하다 자연스럽게 개발자의 꿈을 가지게 되었습니다.<br />
