      <div v-for="(bar, index) in bars" :key="index">
        <span class="bar_name">{{ bar.name }}</span>
        <div class="bar_container">
          <div
            :class="'bar_content'"
            :style="{ width: bar.percentage + '%' }"
          ></div>
          <p :class="'bar_text'">{{ bar.percentage }}%</p>
        </div>
      </div>
    </div>
