</template>
<script>
export default {
  data() {
    return {
      bars: [
        { name: 'HTML & CSS (UI)', percentage: 0, percent: 80 },
        { name: 'Javascript', percentage: 0, percent: 70 },
        { name: 'Vue.js', percentage: 0, percent: 60 },
        { name: 'Node.js', percentage: 0, percent: 60 },
        { name: 'TensorFlow.js', percentage: 0, percent: 50 },
        { name: '데이터베이스 (DB)', percentage: 0, percent: 50 }
      ]
    }
  },
  methods: {
    animateBars() {
      this.bars.forEach((bar) => {
        const interval = setInterval(() => {
          if (bar.percentage < bar.percent) {
            bar.percentage += 1
          } else {
            clearInterval(interval)
          }
        }, 35)
      })
    },
    onload() {
      // let scrollpos = window.scrollY
    }
  },
  mounted() {
    this.animateBars()
    this.onload()

    let heroEl
    let projectEl
    let aboutEl
    let techEl
    let scrollY = 0
    window.addEventListener(
      'scroll',
      function () {
        scrollY = window.scrollY
        if (this.$refs.heroEl) {
          heroEl = this.$refs.heroEl
          projectEl = this.$refs.projectsEl
          aboutEl = this.$refs.aboutEl
          techEl = this.$refs.techEl

          console.log(scrollY)

          if (scrollY >= 0) {
            console.log('hero나오는 지점')
            heroEl.classList.add('on')
          }

          if (scrollY >= 200) {
            console.log('project 나오는 지점')
            projectEl.classList.add('on')
          }

          if (scrollY >= 1200) {
            console.log('about 나오는 지점')
            aboutEl.classList.add('on')
          }
          if (scrollY >= 5000) {
            console.log('tech 나오는 지점')
            techEl.classList.add('on')
          }
        }
      }.bind(this)
    )
  }
}
</script>
